*, *::before, *::after,
*:before, *:after {
  box-sizing: border-box; }

body {
  padding: 0;
  margin: 0;
  font-family: Arial, Helvetica, sans-serif; }

.main-wrapper, .header-wrapper {
  width: 100%;
  max-width: 1140px;
  margin: auto; }

.text-center {
  text-align: center; }

.main-wrapper {
  margin-top: 2rem; }

.ul-reset {
  list-style: none;
  padding-left: 0; }

.flex-container {
  display: flex;
  flex-wrap: wrap; }

.flex-item {
  width: 100%;
  padding-left: 10px;
  padding-right: 10px; }
  .flex-item .flex-p-0 {
    padding-left: 0;
    padding-right: 0; }

.form {
  width: 100%;
  max-width: 300px;
  margin: auto;
  border: 1px solid #dedede;
  border-radius: 5px; }
  .form label, .form input {
    display: block;
    width: 100%; }
  .form label {
    margin-bottom: .3rem; }
  .form input {
    border: 1px solid #dedede;
    height: 35px;
    border-radius: 5px;
    margin-bottom: 1rem;
    padding-left: 15px; }
  .form .form-header, .form .form-body {
    padding: 1rem; }
  .form .form-header {
    background-color: #f0f0f0; }

.btn_custom {
  border: none;
  padding: .7rem 1.8rem;
  border-radius: 5px;
  cursor: pointer; }
  .btn_custom.btn_navy {
    background-color: #2d3b4a;
    color: #ffffff; }
    .btn_custom.btn_navy:hover {
      background-color: #273443; }
  .btn_custom.btn_danger {
    background-color: #d03434;
    color: #ffffff; }
    .btn_custom.btn_danger:hover {
      background-color: #b52828; }

.product-container {
  padding: 1.5rem 1rem;
  background-color: #dedede;
  border-radius: 5px;
  margin-bottom: .5rem;
  border: 1px solid #dedede; }
  .product-container .product-body.text-center {
    display: flex;
    justify-content: space-between;
    align-items: center; }
  .product-container .btn_delete {
    margin-left: 2rem; }

.alert {
  padding: 1rem;
  border-radius: 5px;
  margin-bottom: 1rem; }
  .alert.alert_success {
    background-color: #5dc279;
    color: #ffffff; }
  .alert.alert_danger {
    background-color: #d03434;
    color: #ffffff; }
  .alert.alert_danger {
    background-color: #d03434;
    color: #ffffff; }
  .alert.alert_warning {
    background-color: #F3A746;
    color: #ffffff; }

.header {
  padding: 1rem;
  background-color: #131A21; }
  .header h1 {
    color: #ffffff; }

@media screen and (min-width: 768px) {
  .flex-4 {
    width: 33.3%; }
  .flex-8 {
    width: 66.7%; }
  .form {
    margin-left: 0; } }
